.drip-form--colorpicker {
  display: flex;
}
.colorpicker--show {
  margin-right: 5px;
  padding: 1px;
  width: 18px;
  height: 32px;
  border: 1px solid #ccc;
  cursor: pointer;
}
.colorpicker--show--disabled {
  cursor: unset;
}
.colorpicker--container {
  background: #fff;
}
.colorpicker--container .flexbox-fix input {
  color: #000;
}
.colorpicker--container .ant-tooltip-content {
  background: #fff;
}
.colorpicker--container .ant-tooltip-content .ant-tooltip-inner {
  padding: unset;
  background: #fff;
}
.colorpicker--container .ant-tooltip-arrow:before {
  background: #fff;
}

.check-all-wrapper__line {
  margin: 2px 0 5px;
  width: 100%;
  height: 0;
  border-bottom: 1px solid #eaeaea;
}

.drip-form--dargupload {
  margin: 0 5px;
}

.bf-select-text.babel-ui {
  display: flex;
  width: 100%;
}
.bf-select-text.babel-ui .ant-select {
  width: 30%;
}
.bf-select-text.babel-ui .ant-select .ant-select-selector {
  margin-right: 1px;
  border-radius: 2px 0 0 2px;
}
.bf-select-text.babel-ui .ant-input {
  width: 70%;
  border-radius: 0 2px 2px 0;
}
