.field__sure--container {
  display: flex;
  flex: 1;
  align-items: center;
}
.field__sure--button {
  margin-left: 10px;
  width: 40px;
  height: 32px;
  border: 1px #eee solid;
  text-align: center;
  line-height: 32px;
  cursor: pointer;
  user-select: none;
}
.field__sure--button-disabled {
  background: #eee;
  color: #999;
  cursor: not-allowed;
}

.object-container-err {
  color: #f00;
}
.drip-form_objectContainer {
  min-height: 30px;
}
.drip-form_objectContainer--collapse>div>div {
  background-color: #eff6ff;
}

.array-item--action {
  display: flex;
  flex: 0 0 auto;
  justify-content: center;
  align-items: center;
  padding: 0 8px;
  width: 28px;
  height: 32px;
  outline: none;
  border: none;
  border-radius: 5px;
  background-color: transparent;
  cursor: pointer;
  touch-action: none;
  appearance: none;
}
.array-item--action svg {
  flex: 0 0 auto;
  overflow: visible;
  margin: auto;
  height: 100%;
  fill: #919eab;
}
.array-item--action:hover {
  background-color: rgba(0,0,0,0.05);
}

.array-item--field {
  position: relative;
}
.array-item--field_last-child {
  border-bottom: none;
}
.array-item--field .fa-trash-alt {
  margin-right: 20px;
  color: #2d64ff;
  font-size: 0.9rem;
  opacity: 0.7;
  cursor: pointer;
}
.array-item--header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 4px 0;
  height: 32px;
  background-color: rgba(40,100,255,0.102);
}
.array-item--header:hover .array-item--remove {
  display: block;
}
.array-item--handle {
  display: flex;
  align-items: center;
}
.array-item--remove {
  display: none;
}
.array-item--case {
  padding: 20px 20px 0;
}
.array-item--number {
  margin-left: 20px;
  font-size: 12px;
}
.array-item--add {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 32px;
  border: 1px #d9d9d9 dashed;
  cursor: pointer;
}
.array-item--add_title {
  margin-left: 8px;
}

.drip-form_container--footer {
  display: flex;
  width: 100%;
}
